import React, {Fragment} from 'react';
import {setLicenseDaysSettingsModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";
import Slider from '@material-ui/core/Slider';
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";
import {IconPack} from "../../../Images/IconPack";


const LicenseDaysSetting = (props) => {

    let component = null;

    const clickHandler = ( newLangVal ,e ) => {
        props.setCurrentLanguage(newLangVal);
        props.setLanguageModal(false);

    };
    const [value1, setValue1] = React.useState(7);
    const [value2, setValue2] = React.useState("$14");
    const [value3, setValue3] = React.useState(0);





    const handleChange1 = (event, newValue) => {
        setValue1(newValue);
    };
    const handleChange3 = (event, newValue) => {
        setValue3(newValue);
    };

    const handleChange2 = (event, newValue) => {
        setValue2(event.value);
    };


    if(props.appState.modals.licenseDaysSettings) {
        component = ( <div className="popup-layer">
            <div className="popup-content w400">
                <header>
                    <h3>Days Settings</h3>
                    <div className="close" onClick={()=>{props.setLicenseDaysSettingsModal(false)}}><svg width="12" height="12" viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path opacity="0.7" fillRule="evenodd" clipRule="evenodd" d="M6.65613 5.74086L11.6899 0.707107L10.9828 0L5.94903 5.03375L0.956513 0.0412364L0.249406 0.748343L5.24192 5.74086L0 10.9828L0.707107 11.6899L5.94903 6.44797L11.2322 11.7311L11.9393 11.024L6.65613 5.74086Z" fill="white"/>
                    </svg>
                    </div>
                </header>
                <main>
                    <div className="header">
                        <div>
                            <h6>User: </h6>
                            <p><span className="icon-inside">{IconPack.userIcons.forTable}Ronaldo</span></p>
                        </div>
                        <div>
                            <h6>Days left: </h6>
                            <p className="color-white">{value1}</p>
                        </div>
                    </div>

                    <div className="line"></div>

                    <div className="fieldsWrapper">
                    <TextField
                        label="Days left"
                        fullWidth
                        rows={1}
                        value={value1}
                        onChange={handleChange1}
                        variant="filled"
                        className="col-3"
                    />
                    <TextField
                        label="Balance"
                        fullWidth
                        rows={1}
                        value={value2}
                        onChange={handleChange2}
                        variant="filled"
                        className="col-3"
                    />
                    <TextField
                        label="Spend"
                        fullWidth
                        rows={1}
                        value={value3}
                        onChange={handleChange3}
                        variant="filled"
                        className="col-3"
                    /></div>

                    <Slider
                        step={1}
                        min={0}
                        max={30}
                        value={value1} onChange={handleChange1} />
                    <p>Set a new value for the remaining days. You can edit any field, the value of the remaining fields will change automatically.</p>
                    <div className="btn btn--blue">SAVE CHANGES</div>

                </main>
            </div>
        </div>);
    }

    return (
        <Fragment >
            {component}
        </Fragment>
    );
};

const mapStateToProps = (state) => ({
    appState: state.appOptions.appState
});

const mapDispatchToProps = (dispatch) => {
    return {
        setLicenseDaysSettingsModal: (bool) => dispatch(setLicenseDaysSettingsModal(bool)),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(LicenseDaysSetting);
