import React from 'react';
import {searchData} from "../../../redux/config-shop/config.actions";
import {connect} from "react-redux";
import FormControl from "@material-ui/core/FormControl";
import TextField from "@material-ui/core/TextField/TextField";
import MenuItem from "@material-ui/core/MenuItem";

const LicenseAlert = (props) => {


        let summ = null;
        let counter = 0;

        {props.usersTabData.map((i, key) => {
            console.dir(i);
            if(i.userOptions.isSelected) {
                counter++;
            }

        })};


        if(counter) {
            return (<div className="table_action_bar table_action_bar--license">
                    <div>
                    <div className="btn btn--transparent">Cancel</div>
                    <p className="table_action_bar__info">{counter} users selected</p>
                    </div>
                <div>

                    <FormControl className='w320'>
                        <TextField
                            value={'Date of register'}
                            fullWidth
                            select
                            variant="filled"
                        >
                            <MenuItem key={1} value={'Date of register'}>Date of register</MenuItem>
                            <MenuItem key={2} value={'Date of register1'}>Date of register1</MenuItem>
                        </TextField>
                    </FormControl>

                    <div className="btn btn--blue">APPLY</div>
                </div>


                </div>);
        }else {
            return null;
        }

};


const mapStateToProps = (state) => ({
    usersTabData: state.corporateLicenseUsers.usersTab,
});


export default connect(mapStateToProps )(LicenseAlert);

