import React from 'react';
import cogIcon from './cog.svg';
import {setLicenseDaysSettingsModal} from "../../../redux/app/app.actions";
import {connect} from "react-redux";

const DaysLeft = (props) => {
    return (
        <div className="tableCell-daysLeft">
            <div className="value">
            {props.dataValue}
            </div>
            <div className="days">
                365
            </div>
            <div className="btn" onClick={()=>{
                props.setLicenseDaysSettingsModal(true);
            }}>
                <img src={cogIcon} alt=""/>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => ({
    usersTabData: state.corporateLicenseUsers.usersTab,
});

const mapDispatchToProps = (dispatch) => {
    return {
        setLicenseDaysSettingsModal: (type) => dispatch(setLicenseDaysSettingsModal(type)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(DaysLeft);